.Player {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  flex: 1;
  flex-shrink: 1;
  position: relative;
  padding: 0 2.75rem;
  margin-bottom: 1.375rem;
}

.PlayerContent {
  padding: inherit;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.PlayerControls {
  margin-top: 2rem;
  width: 100%;
}

.PlayButton {
  width: 2.5rem;
  height: 2.5rem;
  background: #1E1E1E;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  cursor: pointer;
  user-select: none;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05), 0px 4px 12px rgba(0, 0, 0, 0.04);
}

.PlayButton:active {
  background: #202020;
}
