.Project {
  border-radius: 6px;
  text-decoration: none;
  transition: box-shadow 0.2s ease-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  position: relative;
  background: #282828;
}

.Video {
  object-fit: cover;
  user-select: none;
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 56.25%;
  width: 100%;
  text-decoration: none;
  cursor: pointer;
  border-radius: 6px;
}

.Project:hover {
  box-shadow: 0 0 0 1px #6f7bd0;
  /* box-shadow: rgba(255, 255, 255, 0.3) 5px 5px, */
  /* rgba(255, 255, 255, 0.2) 10px 10px, rgba(255, 255, 255, 0.1) 15px 15px, */
  /* rgba(255, 255, 255, 0.05) 20px 20px, rgba(255, 255, 255, 0.025) 25px 25px; */
}

.InfoContainer {
  width: 100%;
  height: 2.625rem;
}

.Info {
  background: #1d1d1d;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom-left-radius: 4.5px;
  border-bottom-right-radius: 4.5px;
}

.DateAndName {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  height: 2.0625rem;
}

.Name {
  border: none;
  border-radius: 1px;
  margin: 0;
  padding: 0.2rem 0;
  font-size: 0.8125rem;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-right: 0.4rem;
  transition: box-shadow 0.2s ease-out;
  font-family: inherit;
  background: #1d1d1d;
  color: #ffffff;
}

.Name:focus {
  outline: none;
  background: #282828;
  box-shadow: 0 0 0 1px #6f7bd0;
}

.Date {
  margin: 0;
  font-size: 0.625rem;
  line-height: 0.9375rem;
  color: #878787;
}
.OptionsContainer {
  position: relative;
  width: auto;
  height: auto;
}
.Options {
  width: 1.875rem;
  height: 1.875rem;
  user-select: none;
  border-radius: 100%;
  transition: all 0.1s ease-in;
  cursor: pointer;
  background: #2b2b2b;
  padding: 0.5625rem;
  position: relative;

}

.Options:hover {
  background: #383838;
}

.Dots {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Dot {
  width: 0.15rem;
  height: 0.15rem;
  background: white;
  border-radius: 30px;
}

.Dropdown {
  list-style-type: none;
  background: #2b2b2b;
  width: 8.625rem;
  height: 4.75rem;
  margin: 0;
  padding: 0;
  padding: 0.8125rem 0.375rem;
  position: absolute;
  top: 2.375rem;
  right: 0;
  z-index: 10;
  border-radius: 5px;
}

.DropdownOption {
  color: white;
  border-radius: 6px;
  font-size: 0.75rem;
  line-height: 0.9375rem;
  padding: 0.3125rem 1.125rem;
  user-select: none;
  cursor: pointer;
}

.DropdownOption:hover {
  background: #6f7bd0;
}
