.WordContainer {
  font-weight: normal;
  text-decoration: none;
  display: inline-block;
  flex-shrink: 0;
  min-width: 0;
  white-space: pre;
  min-width: 0;
  height: auto;
  line-height: 20px;
}

.Word {
  white-space: pre;
  height: auto;
  line-height: 32px;
}

.DeletedText {
  line-height: 32px;
  color: rgb(88, 88, 88);
}

.Word::selection,
.Deleted::selection,
.WordInput::selection {
  background: #a4ace2 !important;
  color: rgb(240, 240, 240);
}

.DeletedText::selection,
.Pause::selection {
  background: #a4ace2 !important;
}

.Pause {
  line-height: 32px;
  color: #6f7bd0;
  white-space: pre;
}

.Word:hover,
.Pause:hover {
  background: #a4ace21c;
}

.CurrentWord {
  background: #6f7bd0;
}

.CurrentWord .Word,
.CurrentWord .Pause {
  color: white;
}

.CurrentWord:hover .Word,
.CurrentWord:hover .Pause {
  background: #6f7bd0;
}

.Highlight {
  background: #c5a8a6;
  color: rgb(240, 240, 240);
}

.Highlight:hover .Word,
.Highlight:hover .Pause {
  background: #c5a8a6;
  color: rgb(240, 240, 240);
}

.Deleted {
  position: relative;
  cursor: pointer;
}

.DeletedImgContainer {
  width: 100%;
  height: 100%;
  background: #a4ace2;
  position: absolute;
  top: 0;
  left: 0;
  transition: opacity 0.1s ease-in;
  opacity: 0;
}

.DeletedImg {
  position: relative;
  top: 2px;
  left: 3px;
  width: 60%;
  height: auto;
}

.DeletedImg::selection {
  background: #f3f4f9;
}

.Deleted:hover .DeletedImgContainer {
  opacity: 1;
}

.WordInput {
  font-family: inherit;
  font-size: 1rem;
  background: #cdedba;
  color: #343f2a;
  border: none;
  line-height: 32px;
  padding: 0;
  margin: 0;
  width: 100%;
}

.WordInput:focus {
  outline: none;
}

.InSelection {
  background: #a4ace2 !important;
  color: rgb(240, 240, 240) 
}

.InSelection:hover .Word,
.InSelection:hover .Pause {
  background: #a4ace2 !important;
}
