.Settings {
  width: 100%;
  height: 100%;
  padding-left: 3.625rem;
}

.Heading {
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.25rem;
  margin-top: 1.75rem;
  margin-bottom: 1.25rem;
  color: white;
}

.Form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.Label {
  font-size: 0.75rem;
  text-align: center;
  margin-bottom: 0.5rem;
  color: white;
}

.ButtonSpinnerContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.5rem;
}

.UpdateButton {
  font-family: inherit;
  font-weight: 500;
  font-size: 0.75rem;
  line-height: 1.25rem;
  color: #ffffff;
  width: 6.375rem;
  padding: 0.375rem 0;
  background: #6f7bd0;
  border-radius: 1.875rem;
  border: none;
  cursor: pointer;
  margin-right: 1rem;
}

.Billing {
  margin-top: 2.75rem;
}

.SubHeading {
  font-weight: 600;
  font-size: 1.25rem;
  line-height: 1.25rem;
  margin: 0;
  margin-bottom: 0.75rem;
  color: white;
}

.Plan {
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: #3c3c3c;
  margin: 0;
  margin-bottom: 0.125rem;
  color: white;
}

.Renewal {
  font-size: 0.75rem;
  line-height: 1.25rem;
  color: #999999;
  margin: 0;
  margin-bottom: 1.25rem;
}

.CancelButton {
  font-family: inherit;
  font-size: 0.75rem;
  line-height: 1.25rem;
  padding: 0.375rem 0;
  width: 9.125rem;
  border-radius: 1.875rem;
  border: none;
  cursor: pointer;
  color: #ffffff;
  background: #6f7bd0;
}
