.Template {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 12px;
  width: 20%;
}

.TemplateInner {
  padding-top: 56.25%;
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background: #282828; */
  border-radius: 6px;
  transition: all 0.1s ease-out;
  user-select: none;
  overflow: hidden;
}

.TemplateImgContainer {
  width: 100%;
}

.TemplateImg {
  height: 100%;
  width: auto;
  position: absolute;
  top: 0;
  border-radius: 3px;
  border: 1px solid rgba(255, 255, 255, 0);
  cursor: pointer;
}

.TemplateImg:hover {
  border: 1px solid #6f7bd0;
}

@media (min-height: 600px) and (max-width: 2000px) {
  .Template {
    width: 25%;
  }
}
@media (min-height: 600px) and (max-width: 1600px) {
  .Template {
    width: 33.33%;
  }
}
@media (min-height: 600px) and (max-width: 1100px) {
  .Template {
    width: 50%;
  }
}
@media (min-height: 600px) and (max-width: 800px) {
  .Template {
    width: 100%;
  }
}
