.Transcription {
  background: #1E1E1E;
  max-width: 30.25rem;
  min-width: 20rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1.625rem 0;
  margin-left: 0.375rem;
  flex: 1;
}

.TextControls {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 2rem;
  padding: 0 1.56rem;
}

.CutContainer {
  height: 100%;
  padding: 0 0.75rem;
  border-radius: 4px;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  user-select: none;
  cursor: pointer;
  margin-right: 0.31rem;
  background: #252525;
}

.CutContainer:hover {
  background: #2c2c2c;
}

.CutContainer img {
  margin-right: 0.5rem;
}

.CutContainer p {
  font-size: 0.75rem;
  color: #999999;
  margin: 0;
}

.SearchContainer {
  height: 100%;
  border-radius: 4px;
  padding: 0 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 17.25rem;
  background: #252525;
}

.SearchInput {
  font-family: inherit;
  height: 100%;
  width: 100%;
  padding: 0;
  border: none;
  padding-right: 0.2rem;
  background: #252525;
  color: rgb(223, 223, 223);
}

.SearchInput::placeholder {
  color: #B0B0B0;
}

.SearchInput:focus {
  outline: none;
}

.Text {
  display: block;
  text-align: start;
  margin-top: 1rem;
  padding: 0.5rem 1.56rem 2rem 1.56rem;
  width: 100%;
  min-width: 0;
  overflow-y: auto;
  height: 70vh;
  color: #999999;
}

.Text::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}
.Text::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgb(197, 197, 197);
}
.Text::-webkit-scrollbar-track {
  background: rgba(217, 217, 217, 0.3);
}