.Navbar {
  width: 100%;
  min-height: 3.125rem;
  height: 3.125rem;
  background: #111111;
  display: flex;
  justify-content: space-between;
  padding: 0 2rem;
}

.BackFileInfoContainer {
  width: auto;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex: 1;
}

.BackButton {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.5rem;
  padding: 0.5rem 0.3rem;
  border-radius: 3px;
  transition: background 0.1s ease-out;
  cursor: pointer;
}

.HomeLinkContainer {
  margin-right: 0.25rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.HomeLink {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
  margin-right: 0.25rem;
  border-radius: 3px;
  transition: background 0.1s ease-out;
  cursor: pointer;
}

.BorderLine {
  width: 0.5px;
  height: 1.375rem;
  background: white;
}

.BackButton:hover,
.HomeLink:hover {
  background: #383838;
}

.FileName {
  margin: 0;
  font-size: 0.75rem;
  line-height: 0.9rem;
  white-space: nowrap;
  color: white;
}

.Creater {
  margin: 0;
  font-size: 0.625rem;
  font-weight: 500;
  line-height: 0.75rem;
  color: #999999;
  white-space: nowrap;
}

.EditVideo {
  margin: 0;
  text-decoration: none;
  font-weight: 500;
  font-size: 13px;
  color: white;
  padding: 0.5rem;
  border-radius: 3px;
  transition: background 0.1s ease-out;
}

.EditVideo:hover {
  background: #383838;
}

.UserInfoContainer {
  display: flex;
  align-items: center;
  align-items: center;
  justify-content: flex-end;
  flex: 1;
  margin-left: 1rem;
}

.UserName {
  display: flex;
  align-items: center;
}

.Initials {
  background: #6F7BD0;
  color: white;
  width: 1.875rem;
  height: 1.875rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  user-select: none;
}

.Letter {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.LogInLink {
  text-decoration: none;
  font-weight: 500;
  font-size: 0.875rem;
  cursor: pointer;
  color: #6f7bd0;
  margin-right: 1.25rem;
}

.SignUpLink {
  text-decoration: none;
  font-weight: 500;
  font-size: 0.75rem;
  background: #6f7bd0;
  text-align: center;
  color: #ffffff;
  padding: 0.5rem 1rem;
  border-radius: 30px;
}
