.InputContainer {
  width: 100%;
  margin-bottom: 0.875rem;
}

.Input {
  font-family: inherit;
  width: 100%;
  height: 3.06rem;
  border-radius: 30px;
  font-size: 0.875rem;
  border: 1px solid #333333;
  padding: 0 1.25rem;
  transition: border 0.1s ease-in;
  margin-bottom: 0.375rem;
  background: #333333;
  color: white;
}

.Input:focus,
.Input:-webkit-autofill:focus  {
  outline: none;
  border: 1px solid #6f7bd0;
}

.Input::placeholder {
  color:#999999;
}

.Invalid {
  border: 1px solid #f15165;
}

.ErrorMessage {
  font-size: 0.75rem;
  line-height: 0.9375rem;
  padding-left: 1.25rem;
  color: #f15165;
}

.ErrorMessage::selection {
  background-color: #f15165;
  color: white;
}

.Input:-webkit-autofill {
  border: 1px solid #333333;
  color: white;
  caret-color: white;
  -webkit-text-fill-color: white;
  -webkit-box-shadow: 0 0 0px 1000px #333333 inset;
  box-shadow: 0 0 0px 1000px #333333 inset;
  transition: background-color 5000s ease-in-out 0s;
}