.Dropdown {
  height: 9.5rem;
  width: 53rem;
  position: relative;
  background: #1E1E1E;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.05), 4px 4px 12px rgba(0, 0, 0, 0.04);
  border-radius: 6px;
  padding-left: 1.375rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.TemplateOptions {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.TemplateOption {
  cursor: pointer;
  font-size: 0.75rem;
  padding: 0.75rem 0.2rem 0.6rem;
  margin-right: 1.125rem;
  color: #999999;
}

.Current {
  color: #6f7bd0;
  font-weight: bold;
}

.Templates {
  list-style-type: none;
  margin: 0;
  padding: 0;
  padding-bottom: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
  overflow-y: hidden;
  overflow-x: auto;
}

.Template {
  cursor: pointer;
  height: 100%;
  width: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.5625rem;
  position: relative;
  margin-top: 2px;
  margin-left: 2px;
}

.TemplateImg {
  height: 100%;
  width: auto;
}

.Template:hover  {
  box-shadow: 0 0 0 1px #6f7bd0;
}

.SaveTemplate {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 5rem;
  text-align: center;
  border-radius: 6px;
  color: white;
  box-shadow: 0 0 0 1px #999999;
}

.SaveTemplate:hover {
  background: rgba(88, 110, 224, 0.06);
}

.SaveImg {
  width: 2rem;
  height: auto;
  flex: 1;
  margin-top: 0.5rem;
}

.SaveText {
  width: 100%;
  font-size: 0.7rem;
  flex: 1;
}

.Templates::-webkit-scrollbar {
  height: 5px;
}
.Templates::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: rgb(197, 197, 197);
}
.Templates::-webkit-scrollbar-track {
  background: rgba(217, 217, 217, 0.3);
}

.DeleteContainer {
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(88, 110, 224, 0.3);
  border-radius: 100%;
  position: absolute;
  top: 6px;
  right: 6px;
  opacity: 0;
}

.XLine1 {
  display: block;
  width: 0.8rem;
  height: 0.0625rem;
  background: white;
  position: absolute;
  transform: rotateZ(45deg);
}

.XLine2 {
  display: block;
  width: 0.8rem;
  height: 0.0625rem;
  background: white;
  position: absolute;
  transform: rotateZ(-45deg);
}

.Template:hover .DeleteContainer {
  opacity: 1;
}
.DeleteContainer:hover {
  opacity: 1;
  background: rgba(111, 122, 208, 0.8);
}
.DeleteContainer:hover .XLine1,
.DeleteContainer:hover .XLine2 {
  background: white;
}

.DeleteContainer:hover .Template {
  border: 1px solid #6f7bd0;
}
