.SideDrawer {
  width: 16.18rem;
  height: 100vh;
  padding-top: 1rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  background: #111111;
}

.Links {
  display: flex;
  flex-direction: column;
  padding-left: 1.25rem;
  margin-top: 1.875rem;
  margin-right: 1rem;
}

.Link {
  text-decoration: none;
  color: #999999;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0.375rem 0.6875rem;
  width: 12.75rem;
  margin-bottom: 0.5rem;
}

.Link:active {
  color: #8b8b8b;
}

.LinkText {
  margin-left: 0.6875rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.Active {
  color: #6f7bd0;
}
