.InputContainer {
  width: 100%;
  margin-bottom: 0.75rem;
}

.Input {
  font-family: inherit;
  font-size: 0.75rem;
  padding: 0.5rem 0.875rem;
  background: #333333;
  color: #999999;
  border: 0.0625rem solid #333333;
  border-radius: 0.25rem;
  transition: border 0.1s ease-in;
  width: 16rem;
}

.Input:focus,
.Input:-webkit-autofill:focus {
  outline: none;
  border: 1px solid #6f7bd0;
}

.Input::placeholder {
  color: #8b8b8b;
}

.Invalid {
  border: 1px solid #f15165;
}

.ErrorMessage {
  font-size: 0.75rem;
  line-height: 0.9375rem;
  padding-top: 0.2rem;
  color: #f15165;
}

.ErrorMessage::selection {
  background-color: #f15165;
  color: white;
}

.Input:-webkit-autofill {
  border: 1px solid #333333;
  color: #8b8b8b;
  caret-color: #8b8b8b;
  -webkit-text-fill-color: #8b8b8b;
  -webkit-box-shadow: 0 0 0px 1000px #333333 inset;
  box-shadow: 0 0 0px 1000px #333333 inset;
  transition: background-color 5000s ease-in-out 0s;
}