.User {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 1.75rem;
  width: 100%;
  position: relative;
}

.UserName {
  display: flex;
  align-items: center;
}

.Initials {
  background: #6f7bd0;
  color: white;
  width: 1.875rem;
  height: 1.875rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  user-select: none;
}

.Letter {
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.Name {
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: #3c3c3c;
  width: 8rem;
  margin-left: 0.75rem;
  overflow: hidden;
  text-overflow: ellipsis;
  color: white;
}

.DropdownContainer {
  margin-right: 1.4375rem;
  margin-left: auto;
  height: 1.5rem;
  width: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  cursor: pointer;
}

.DropdownContainer:hover {
  background: #2B2B2B;
}

.Dots {
  width: 0.8125rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.Dropdown {
  height: auto;
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
  bottom: -2.6rem;
  right: 1.4375rem;
  display: flex;
  height: auto;
  border-radius: 6px;
  overflow: hidden;
  background: #1e1e1e;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 6px 28px rgba(0, 0, 0, 0.05);
  user-select: none;
}

.SignOut {
  color: #6F7BD0;
  width: 100%;
  text-align: center;
  cursor: pointer;
  padding: 0.5rem 1rem;
  font-weight: 500;
  font-size: 0.75rem;
  line-height: 1.25rem;
}

.SignOut:hover {
  background: #222222;
}
