.CropCanvas {
  position: fixed;
  z-index: 30;
  top: 0;
  left: 0;
  display: none;
}

.Backdrop {
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: 20;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
