.Templates {
  width: 100%;
  height: 100%;
  padding: 1rem 1rem 4rem 0rem;
  overflow-y: auto;
}

.Templates::-webkit-scrollbar {
  width: 8px;
}
.Templates::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: #d9d9d9;
}
.Templates::-webkit-scrollbar-track {
  background: rgba(217, 217, 217, 0.3);
}

.TemplateTypes {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding-left: 3.625rem;
  list-style-type: none;
  user-select: none;
}

.TemplateType {
  font-size: 0.875rem;
  line-height: 1.25rem;
  text-decoration: none;
  color: #888888;
  cursor: pointer;
  border-radius: 30px;
  padding: 0.4rem 0.8125rem;
}

.Current {
  background: rgba(111, 123, 208, 0.8);
  color: white;
}